<template>
  <el-container>
    <el-header>
      <HeaderBar/>
    </el-header>

    <el-main>
      <div v-show="$store.state.componentVisibility.toolSideBar">
        <SideBar/>
        <ParameterPanel v-if="$store.state.componentVisibility.parameterPanel"/>
      </div>

      <Model/>
    </el-main>
  </el-container>
</template>

<script>
    import Model from "./Model";
    import HeaderBar from "./HeaderBar";
    import SideBar from "./SideBar";
    import ParameterPanel from "./ParameterPanel";
    import componentVisibility from "../../store/componentVisibility";

    export default {
        name: "HomePage",
        components: {SideBar, HeaderBar, Model, ParameterPanel},
        data() {
            return {

            }
        },

        methods: {}
    }
</script>

<style scoped>
  .el-header, .el-main {
    padding: 0;
  }

  .el-menu.el-menu.el-menu--horizontal {
    border-bottom: none;
  }
</style>


