
const componentVisibility = {
    state: {
        toolSideBar: true,
        parameterPanel: true,
    },
    mutations: {
        changeToolSideBar(state, payload){
            state.toolSideBar = payload.visible;
        },
        changeParameterPanel(state, payload){
            state.parameterPanel = payload.visible
        }
    }
}

export default componentVisibility;


