<template>
  <VueDraggableResize :w="320" :x="350" :y="80"
                 :isResizable="false" :handle-info="{size: 0,offset: 0,switch: true}">
    <el-container class="parameter-panel">
      <el-header height="30px">
        <el-row>
          <el-col :span="22" :offset="1">工具1</el-col>
          <el-col :span="1">
            <el-link :underline="false" type="danger" icon="el-icon-close" v-on:click="close"></el-link>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <el-form class="param-form" ref="form" :model="paramForm"
                 label-width="80px" size="mini" label-position="right">
          <el-form-item label="阈值">
            <el-input v-model="paramForm.threshold" type="number"></el-input>
          </el-form-item>

          <el-form-item label="约束条件">
            <el-checkbox-group v-model="paramForm.condition">
              <el-checkbox label="约束1" name="type"></el-checkbox>
              <el-checkbox label="约束2" name="type"></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="模型类型">
            <el-radio-group v-model="paramForm.type">
              <el-radio label="四面体"></el-radio>
              <el-radio label="六面体"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
      </el-main>
      <el-footer height="40px">
        <el-row>
          <el-button type="info" size="mini" v-on:click="close">取消</el-button>
          <el-button type="primary" size="mini" v-on:click="close">应用</el-button>
        </el-row>
      </el-footer>
    </el-container>
  </VueDraggableResize>
</template>

<script>
    import VueDraggableResize from 'vue-draggable-resizable-gorkys'

    export default {
        name: "ParameterPanel",
        components: {VueDraggableResize},
        data() {
            return {
                paramForm: {
                    threshold: 1,
                    condition: [],
                    type: ''
                }
            }
        },
        methods:{
            close() {
                this.$store.commit('changeParameterPanel', {visible: false})
            }
        }
    }
</script>

<style>
  .parameter-panel {
    background-color: #545c64;
    border-radius: 10px;
    width: 320px;
    padding: 10px 5px 0 5px;
    color: white;
    border: solid #474747 1px;
  }

  .el-form-item__label, .el-radio__label, .el-checkbox__label {
    color: white;
  }

  .vdr.active:before {
    outline: none;
  }

</style>


