<template>
    <el-menu
            :default-active="activeIndex"
            class="el-menu-demo"
            mode="horizontal"
            @select="handleSelect"
            background-color="#545c64"
            text-color="#fff"
            active-text-color="#409EFF">
        <el-menu-item >
            <el-avatar size="large" :src="logoUrl"></el-avatar>
        </el-menu-item>
        <el-submenu index="1">
            <template slot="title">文件</template>
            <el-menu-item index="load">加载</el-menu-item>
            <el-menu-item index="save">保存</el-menu-item>
            <el-menu-item index="exit">退出</el-menu-item>
        </el-submenu>
        <el-submenu index="2">
            <template slot="title">编辑</template>
            <el-menu-item index="2-1">选项1</el-menu-item>
            <el-menu-item index="2-2">选项2</el-menu-item>
            <el-menu-item index="2-3">选项3</el-menu-item>
        </el-submenu>
        <el-submenu index="3">
            <template slot="title">视图</template>
            <el-menu-item index="reset">复位</el-menu-item>
            <el-menu-item index="3-2">选项2</el-menu-item>
            <el-menu-item index="3-3">选项3</el-menu-item>
        </el-submenu>
        <el-submenu index="4">
            <template slot="title">窗口</template>
            <el-menu-item index="toolSideBarVisibility">
                {{ $store.state.componentVisibility.toolSideBar ? "隐藏": "显示" }}侧边工具箱
            </el-menu-item>
            <el-menu-item index="4-2">选项2</el-menu-item>
            <el-menu-item index="4-3">选项3</el-menu-item>
        </el-submenu>
    </el-menu>
</template>

<script>
    export default {
        name: "HeaderBar",
        data() {
            return {
                logoUrl: 'static/logo.png',
                activeIndex: '1',
            };
        },
        methods: {
            handleSelect(key, keyPath) {
                switch (key) {
                    case 'reset':
                        this.$EventBus.$emit("model", {action: key});
                        break;

                    case 'toolSideBarVisibility':
                        this.$store.commit('changeToolSideBar',
                                {visible: !this.$store.state.componentVisibility.toolSideBar}
                                )
                        break;
                }

            }
        }
    }
</script>

<style scoped>

</style>


