<template>
  <VueDraggableResize :w="320" :x="20" :y="80"
                      :isResizable="false" :handle-info="{size: 0,offset: 0,switch: true}">
    <el-container class="side-bar">
      <el-header height="30px">
        <el-row>
          <el-col :span="22" :offset="1">工具箱</el-col>
          <el-col :span="1">
            <el-link :underline="false" type="danger" icon="el-icon-close" v-on:click="close"></el-link>
          </el-col>
        </el-row>
      </el-header>

      <el-main>
        <el-tabs tab-position="left" v-model="activeTabName" stretch style="margin: 0 10px;">
          <el-tab-pane label="创建" name="first">
            <el-menu
                    default-active="1"
                    @open="handleOpen"
                    @close="handleClose"
                    text-color="#fff"
                    background-color="#545c64"
                    active-text-color="#ffd04b"
                    v-bind:style="{height: maxHeight + 'px'}"
                    style="text-align: left; overflow: auto">
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span>导航一</span>
                </template>
                <el-menu-item-group>
                  <template slot="title">分组一</template>
                  <el-menu-item index="1-1">选项1</el-menu-item>
                  <el-menu-item index="1-2">选项2</el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group title="分组2">
                  <el-menu-item index="1-3">选项3</el-menu-item>
                </el-menu-item-group>
                <el-submenu index="1-4">
                  <template slot="title">选项4</template>
                  <el-menu-item index="1-4-1">选项1</el-menu-item>
                </el-submenu>
              </el-submenu>
              <el-menu-item index="2">
                <i class="el-icon-menu"></i>
                <span slot="title">导航二</span>
              </el-menu-item>
              <el-menu-item index="3" disabled>
                <i class="el-icon-document"></i>
                <span slot="title">导航三</span>
              </el-menu-item>
              <el-menu-item index="4">
                <i class="el-icon-setting"></i>
                <span slot="title">导航四</span>
              </el-menu-item>
            </el-menu>
          </el-tab-pane>
          <el-tab-pane label="建模" name="second">
            <div v-bind:style="{height: maxHeight + 'px'}" style="overflow: auto">
              <el-collapse style="background-color: #545c64">
                <el-collapse-item v-for="z in 8" :key="z" :name="z" style="background-color: #545c64">
                  <template slot="title">
                    <i class="el-icon-location"> </i>
                    <span>建模工具{{ z }}</span>
                  </template>
                  <el-row v-for="i in 3" :key="i">
                    <el-col :span="8" v-for="j in 3" :key="j">
                      <div class="grid-content bg-purple" v-on:click="showParamPanel">
                        <el-tooltip class="item" effect="dark" content="网格工具" placement="top-start">
                          <el-avatar shape="square" :size="60" fit="fill" class="functional-card"
                                     src="/static/logo.png"></el-avatar>
                        </el-tooltip>
                      </div>
                    </el-col>
                  </el-row>
                </el-collapse-item>
              </el-collapse>
            </div>
          </el-tab-pane>
          <el-tab-pane label="网格" name="third">
            <div v-bind:style="{height: maxHeight + 'px'}" style="overflow: auto">
              <el-collapse style="background-color: #545c64">
                <el-collapse-item v-for="z in 8" :key="z" :name="z" style="background-color: #545c64">
                  <template slot="title">
                    <i class="el-icon-location"> </i>
                    <span>网格工具{{ z }}</span>
                  </template>
                  <el-row v-for="i in 3" :key="i">
                    <el-col :span="8" v-for="j in 3" :key="j">
                      <div class="grid-content bg-purple" v-on:click="showParamPanel">
                        <el-tooltip class="item" effect="dark" content="网格工具" placement="top-start">
                          <el-avatar shape="square" :size="60" fit="fill" class="functional-card"
                                     src="/static/logo.png"></el-avatar>
                        </el-tooltip>
                      </div>
                    </el-col>
                  </el-row>
                </el-collapse-item>
              </el-collapse>
            </div>
          </el-tab-pane>
        </el-tabs>
      </el-main>
    </el-container>
  </VueDraggableResize>
</template>

<script>
    import VueDraggableResize from 'vue-draggable-resizable-gorkys'

    export default {
        name: "SideBar",
        components: {VueDraggableResize},
        data() {
            return {
                maxHeight: 20,
                activeTabName: "first",
            }
        },
        mounted() {
            this.maxHeight = window.innerHeight * 2 / 3;
            window.addEventListener('resize', e => {
                this.maxHeight = window.innerHeight * 2 / 3
            }, false);
        },
        methods: {
            close() {
                this.$store.commit('changeToolSideBar', {visible: false})
            },
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            showParamPanel() {
                this.$store.commit('changeParameterPanel', {visible: true})
            },

        }
    }
</script>

<style>
  .side-bar {
    background-color: #545c64;
    border-radius: 10px;
    padding: 10px 2px 0 2px;
    text-align: center;
    color: white;

    border: solid #474747 1px;
  }

  .vdr.active:before {
    outline: none;
  }

  /*.el-header {*/
  /*  !*padding: 0;*!*/
  /*  padding: 10px 15px 0 5px;*/
  /*  text-align: center;*/
  /*  color: white;*/
  /*}*/

  .el-main {
    padding: 0 5px 0 5px;
  }

  .el-menu {
    border: none;
  }

  .el-tabs__item {
    color: white;
  }

  .el-tabs__item .el-tabs__item.is-active {
    color: white;
  }

  .el-collapse-item__header, .el-collapse-item__content {
    background-color: #545c64;
    border: none;
    padding: 0 20px;
    font-size: 14px;
  }

  .el-collapse-item__header {
    height: 56px;
    line-height: 56px;
  }

  .el-collapse-item__wrap {
    background-color: #545c64;
  }

  .el-collapse-item__header i {
    color: #909399;
    font-size: 18px;
    margin-right: 10px;
  }

  .el-collapse-item__header span {
    color: white;
    font-weight: normal;
  }

  .el-collapse-item__header:hover {
    background-color: rgb(64, 74, 80);
  }

  .el-collapse {
    border: none;
  }

  .functional-card {
    background-color: transparent;
    border: none;
    padding: 7px;
  }

  .functional-card:hover {
    background-color: rgb(64, 74, 80);
  }
</style>


